
@import "../component.scss";


.board-detail {
	.detail-summary {
		padding: 65px 20px 17px;
		border-bottom: 1px solid #d1d1d1;
	}
	.detail-title {
		margin-bottom: 10px;
		color: #333;
		font : {
			size: 24px;
			weight: 600;
		}
	}
	.meta{
		font-size: 14px;
		color: #333;
		& > span{
			position: relative;
			line-height: 1.6;
		}
		&-category{
			display: inline-block;
			margin-right: 5px;
			padding-right: 10px;
			&:after{
				content: '';
				position: absolute;
				top: 50%;
				right: 0;
				display: inline-block;
				width: 1px;
				height: 14px;
				margin-top: -7px;
				background-color: #333;
			}
		}
		&-date {
			display: inline-block;
			font-size: 14px;
		}
		&-volunteer-date{
			display: block;
			color: #0061a0;
			.total{
				position: relative;
				padding-left: 6px;
				margin-left: 2px;
				&:after{
					content: '';
					position: absolute;
					top: 50%;
					left: 0;
					display: inline-block;
					width: 1px;
					height: 14px;
					margin-top: -7px;
					background-color: #0061a0;
				}
			}
		}
		&-file {
			display: block;
			margin-top: 28px;
			font-size: 12px;
			color: #0061a0;
		}
	}
	.detail-contents {
		padding: 68px 0;
		border-bottom: 1px solid #d1d1d1;
		line-height: 1.5;
		font-size: 16px;
		color: #333;

		img {
			max-width: 100%;
			margin-bottom: 30px;
		}
		p {
			padding: 0 22px;
		}
	}
}